.modal-container{
     /* display: none; */
    position: fixed; 
    z-index: 1; 
    padding-top: 100px; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto; 
    background-color: rgba(0,0,0,0.4);
}
.modal{
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: fit-content;
    border-radius: 10px;
    position: relative;
}
.cancel-add-btn{
    width: 751px;
    margin: auto;
    display: flex;
    justify-content: end;
    padding: 16px 0;
}
.cancel-add-btn > button:nth-child(1){
    width: 95px;
    height: 26px;
    border-radius: 10px;
    border: transparent;
    cursor: pointer;
}
.cancel-add-btn > button:nth-child(2){
    width: 95px;
    height: 26px;
    border-radius: 10px;
    border: transparent;
    margin-left: 10px;
    background-color: #f9dd07;
    cursor: pointer;
}
.no-contacts{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}
.no-contacts > div > i{
    transform: rotate(90deg);
    font-size: 37px;
}
.no-contacts > div{
    margin-top: 20px;
    background: #e8e8e8;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
}